import React, { useState } from 'react'
import {Link} from 'react-router-dom'
import calender from "../../assets/img/calender.svg"
function UpcomingCard({dates,heading,description,imgsrc,eventid}) {
  return (
    
    <div key={eventid} className="bg-gray-800 bg-opacity-40 rounded-2xl">
          <img className="rounded rounded-t-2xl w-full" src={imgsrc} alt="content"/>
          <div className='p-6 pt-2'>
          <h3 className="tracking-widest mb-2 text-white/70 font-bold  text-md title-font">{heading}</h3>
          <div className='flex mb-2'>
          <img className='mr-1' src={calender} alt='icon'/>
          <h2 className="text-lg text-skin-purple font-medium title-font">{dates}</h2>

          </div>
          <p className="leading-relaxed font-semibold mb-2 text-white/70">{description.length < 60 ? description : (description.slice(0, 60) + '...')}.</p>
          <Link to={`/events/${eventid}`} className="bg-skin-purple  text-md px-2 p-1 rounded-md text-white/70 font-bold title-font">Read More</Link>
          </div>
    </div>
  )
}

export default UpcomingCard
