import React from 'react'
import { useParams} from 'react-router-dom'
import EventPageTop from '../Sections/EventPageTop/EventPageTop'
import EventPageBottom from '../Sections/EventPageBottom/EventPageBottom'

function EventsPage() {
   const {id} = useParams();
  return (
    <>
    <div>
      <EventPageTop />
    </div>
    <div>
      <EventPageBottom/>
    </div>
    
    </>
  )
}

export default EventsPage