import React from 'react'
import PageTitle from '../Components/PageTitle'


function Contactusbox(){
  return(
  <section className="text-skin-base body-font my-auto justify-center ">
  <div className="container px-5 pt-10 pb-14 mx-auto flex sm:flex-nowrap flex-wrap">
    <div  className="lg:w-2/3 md:w-1/2 rounded-lg overflow-hidden sm:mr-10 p-10 flex items-end justify-start relative">
      <iframe width="100%" height="100%" className="absolute inset-0" frameBorder="0" title="map" marginHeight="0" marginWidth="0" scrolling="no"   src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3561.89467912516!2d75.87482951538466!3d26.77962697225273!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x396dc9d5c3d68749%3A0xa227c3f0db21d4fc!2sPoornima%20University!5e0!3m2!1sen!2sin!4v1653852799902!5m2!1sen!2sin"></iframe>
      <div className="bg-skin-fill-50 relative flex flex-wrap py-6 rounded shadow-md">
        <div className="lg:w-1/2 px-6">
          <h2 className="title-font font-semibold tracking-widest text-xs">ADDRESS</h2>
          <p className="mt-1">Plot No. IS-2027-2031, Ramchandrapura P.O. Vidhani Vatika Road Sitapura, Rajasthan 303905</p>
        </div>
        <div className="lg:w-1/2 px-6 mt-4 lg:mt-0">
          <h2 className="title-font font-semibold tracking-widest text-xs">EMAIL</h2>
          <a href='#/' className="text-indigo-500 leading-relaxed">info.poornima.edu.in</a>
          <h2 className="title-font font-semibold tracking-widest text-xs mt-4">PHONE</h2>
          <p className="leading-relaxed">08875666617</p>
        </div>
      </div>
    </div>
    <div className="lg:w-1/2 md:w-1/2 bg-transparent px-10  flex flex-col md:ml-auto w-full md:py-4 mt-4 md:mt-0">
      <h2 className=" text-lg mb-1 font-medium title-font">Feedback</h2>
      <p className="leading-relaxed mb-3 text-skin-base">Send us feedback, problem and suggestion Or any query realted to business and event</p>
      <div className="relative mb-3">
        <label htmlFor="name" className="leading-7 text-sm ">Name</label>
        <input type="text" id="name" name="name" className="w-full bg-skin-fill-2  rounded border border-3 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-200 text-base outline-none py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"/>
      </div>
      <div className="relative mb-3">
        <label htmlFor="email" className="leading-7 text-sm ">Email</label>
        <input type="email" id="email" name="email" className="w-full bg-skin-fill-2 rounded border border-3 focus:border-3 focus:ring-2 focus:ring-indigo-200 text-base outline-none py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"/>
      </div>
      <div className="relative mb-3">
        <label htmlFor="message" className="leading-7 text-sm ">Message</label>
        <textarea id="message" name="message" className="w-full bg-skin-fill-2  rounded border border-3 focus:border-3 focus:ring-2 focus:ring-indigo-200 h-24 text-base outline-none  py-1 px-3 resize-none leading-6 transition-colors duration-200 ease-in-out"></textarea>
      </div>
      <button className="text-white bg-skin-button-muted border-0 py-2 px-6 focus:outline-none hover:bg-indigo-600 rounded text-lg">Button</button>
    </div>
  </div>
</section>
  )
}

function Contactus() {
  return (
    <div >
    <PageTitle title='CONTACT US'/>
    <Contactusbox/>
    </div>
  )
}

export default Contactus