import React from 'react'
import { Link } from 'react-router-dom'
import calender from "../../assets/img/calender.svg"
import time from "../../assets/img/time.svg"

function EventCard({dates,heading,imgsrc,eventid,eventtime}) {
  return (
    <div  className="container rounded-xl ease-in-out bg-gradient-to-r p-1 from-plupler to-black hover:from-black hover:to-plupler  ">
    <div  className="bg-gray-800   bg-opacity-40 rounded-md">
          <img className="xl:h-50 md:h-30  rounded rounded-t-xl w-full "  src={imgsrc} alt="img"/>
          <div className='p-6 pt-2'>
          <h3 className="tracking-widest mb-2 text-white/70 font-bold text-center  text-xl title-font">{heading}</h3>
          <div className='flex justify-center'>     
          <div className='mb-2 pr-5 border-r-2  border-white/50'>
          <img className='m-auto' src={calender} alt='icon'/>
          <h6 className='text-center text-xs text-skin-purple font-medium'>Date</h6>
          <h2 className="text-xs text-white mt-1 title-font">{dates}</h2>
          </div>
          <div className='mb-2 pl-5'>
          <img className='m-auto  ' src={time} alt='icon'/>
          <h6 className='text-center text-xs text-skin-purple font-medium'>Time</h6>
          <h2 className="text-xs text-white mt-1 title-font">{eventtime}</h2>
          </div>
          </div>
          <div className='flex mt-4 justify-between'>
          <Link to={`/events/${eventid}`} className="bg-skin-purple text-sm mr-1  px-3 py-1 rounded-md text-white/70  title-font">Registration</Link>
          <Link to={`/events/${eventid}`} className="bg-skin-purple text-sm ml-1 px-3 py-1 rounded-md text-white/70  title-font">View More</Link>
          </div>
          </div>
    </div>
    </div>
  )
}

export default EventCard