import React, { useState } from 'react'
import {Link, Navigate} from 'react-router-dom'
import pulogo from '../../assets/img/pulogo.svg'
import loign from '../../assets/img/login.svg'
import axios from 'axios';

function Register() {
    const [name ,setName] = useState('');
    const [password, setPassword] = useState('');
    const [confermPassword, setconfermPassword] = useState('');
    const [navigate ,setNavigate] = useState (false);
    const [email, setEmail] = useState('');

    const submit = async e => {
        e.preventDefault();

        await axios.post('https://pueventapi.herokuapp.com/login/user',{name,password,email});

        setNavigate(true);
    }
    if (navigate) {
        return <Navigate to="/login"/>;
    }
  return (
         <div className="relative flex flex-col justify-center m-4  overflow-hidden">

            <div className=" flex w-full flex-col sm:flex-row text-white p-6 m-auto bg-skin-fill-100 rounded-3xl shadow-xl lg:max-w-6xl">
             <div  className='p-6 w-full  my-auto'>
            <img className=' w-[50%] mx-auto mb-9 ' src={pulogo} alt='logo'></img>
            <img src={loign} alt='logo'></img>
            </div>
            <div className='w-full self-center'>
                 <h1 className="text-3xl font-extrabold text-left ">
                   SINGUP
                </h1>
                <div className="mt-6">
                    <div className="mb-2">
                        <label
                            htmlFor="email"
                            className="block text-sm font-semibold text-skin-base"
                        >
                            Name
                        </label>
                        <input
                            type="email"
                            className="block w-full px-4 py-2 mt-2  bg-skin-fill-2 border border-3 rounded-md focus:border-purple-400 focus:ring-purple-300 focus:outline-none focus:ring focus:ring-opacity-40"
                            placeholder="Name"
                            onChange={e => setName(e.target.value)}
                        />
                    </div>
                    <div className="mb-2">
                        <label
                            htmlFor="email"
                            className="block text-sm font-semibold text-skin-base"
                        >
                            Account Type
                        </label>
                            <div class="relative">
                        <select class="block appearance-none w-full bg-skin-fill-2 border border-3 text-skin-base py-3 px-4 pr-8 rounded leading-tight focus:outline-none focus:bg-skin-fill-2 ">
                        <option>Select dropdown</option>
                        <option>With options</option>
                        </select>
                         <div class="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-skin-base">
                        <svg class="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z"/></svg>
                       </div>
                       </div>
                        
                    </div>
                   <div className="mb-2">
                        <label
                            htmlFor="email"
                            className="block text-sm  font-semibold "
                        >
                            Email
                        </label>
                        <div className='flex'>
                        <input  
                            type="email"
                            className="block w-full px-4 py-1  bg-skin-fill-2 border border-3 rounded-l-md foc0us:border-3 focus:ring-purple-300 focus:outline-none focus:ring focus:ring-opacity-40"
                            placeholder="Email"
                        />
                        <button className='px-4 py-2  text-xl font-bold transition-colors duration-200 transform bg-skin-fill-200 rounded-md hover:bg-skin-fill-200/40 focus:outline-none focus:bg-skin-fill-200/60 ml-[-10px]'>Verify</button>
                        </div>
                    </div>
                    <div className="mb-2">
                        <label
                            htmlFor="email"
                            className="block text-sm font-semibold text-skin-base"
                        >
                            OTP
                        </label>
                        <input
                            type="email"
                            className="block w-full px-4 py-2 mt-2 bg-skin-fill-2 border border-3 rounded-md focus:border-3 focus:ring-purple-300 focus:outline-none focus:ring focus:ring-opacity-40"
                            placeholder="OTP"
                        />
                    </div>
                    <div className="mb-2">
                        <label
                            htmlFor="email"
                            className="block text-sm font-semibold text-skin-base"
                        >
                            Password
                        </label>
                        <input
                            type="email"
                            className="block w-full px-4 py-2 mt-2 bg-skin-fill-2 border border-3 rounded-md focus:border-3 focus:ring-purple-300 focus:outline-none focus:ring focus:ring-opacity-40"
                            placeholder="Password"
                        />
                    </div>
                    <div className="mb-2">
                        <label
                            htmlFor="password"
                            className="block text-sm font-semibold text-skin-base"
                        >
                            Retype Password
                            
                        </label>
                        <input
                            type="password"
                            className="block w-full  px-4 py-2 mt-2  bg-skin-fill-2 border border-3 rounded-md focus:border-purple-400 focus:ring-purple-300 focus:outline-none focus:ring focus:ring-opacity-40"
                            placeholder="Retype Password"
                        />
                    </div>
                
                    <div className="mt-6">
                        <button onClick={submit} className="w-full px-4 py-2 tracking-wide transition-colors duration-200 transform bg-purple-700 rounded-md hover:bg-purple-600 focus:outline-none focus:bg-purple-600">
                            Login
                        </button>
                    </div>
                </div>
                 <p className="mt-8 text-lg font-semibold ">
                    {" "}
                    Already have Account? {" "}
                    <Link to="/login" className="text-skin-pink hover:underline">
                         Login Account
                    </Link>
                </p>
            </div>
               
            </div>
        </div>
  )
}

export default Register