import React from 'react'
import { Link } from 'react-router-dom'
import Verifylogo from '../../assets/img/verification.svg'
import Pulogo from '../../assets/img/pulogo.svg'

// function verification for forgot password

function Verification() {
  return (
      <div className="m-auto justify-center overflow-hidden ">
          <div className=" flex bg-skin-fill-100 w-full sm:flex-row flex-col p-6 m-auto rounded-3xl shadow-xl  lg:max-w-6xl">
            <div  className=' text-white'>
                <div className="w-full p-6 m-auto rounded-md  lg:max-w-xl">
                <img className='  mb-4' src={Pulogo} alt='logo'>
                </img> 
                <h1 className="text-3xl font-semibold ">
                   Verification
                </h1>
                <div className="mt-6">
                    <div className="mb-2">
                        <label
                            htmlFor="email"
                            className="block text-sm font-semibold "
                        >
                            Email
                        </label>
                        <div className='flex'>
                        <input  
                            type="email"
                            className="block w-full px-4 py-1 text-white bg-transparent border border-3 rounded-l-md foc0us:border-3 focus:ring-purple-300 focus:outline-none focus:ring focus:ring-opacity-40"
                            placeholder="Email"
                        />
                        <button className='px-4 py-2  text-white text-xl font-bold transition-colors duration-200 transform bg-skin-fill-200 rounded-md hover:bg-skin-fill-200/40 focus:outline-none focus:bg-skin-fill-200/60 ml-[-10px]'>Verify</button>
                        </div>
                    </div>
                    <div className="mb-2">
                        <label
                            htmlFor="password"
                            className="block text-sm font-semibold"
                        >
                            Verification Code
                        </label>
                        <input 
                            type="password"
                            className="block w-full px-4 py-2 mt-2 text-white bg-transparent  border border-3 rounded-md focus:border-3 focus:ring-purple-300 focus:outline-none focus:ring focus:ring-opacity-40"
                            placeholder="Password"
                        />
                    </div>
                    <div className="mt-6">
                        <button  className="w-full px-4 py-2 tracking-wide text-white text-2xl font-bold transition-colors duration-200 transform bg-skin-button-muted rounded-md hover:bg-skin-button-muted/60 focus:outline-none focus:bg-skin-button-muted/60">
                            Login
                        </button>
                    </div>
                </div>
                <p className="mt-8 text-lg font-semibold ">
                    {" "}
                    Don't have an account?{" "}
                    <Link to="/register" className="text-skin-pink hover:underline">
                        Create Account
                    </Link>
                </p>
            </div>
            </div>
            <div className='w-[50%] self-center'>
                <img src={Verifylogo} alt='logo'></img>
            </div>
            
        </div>
    </div>
  )
}

export default Verification