import React ,{useRef,useEffect,useState}from 'react'
import { upcomingData } from '../temp';

import SectionTitle from '../Components/SectionTitle'
import UpcomingCard from '../Components/Card/UpcomingCard';
import {Swiper,SwiperSlide} from 'swiper/react';
import { Pagination, Navigation } from "swiper";
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
function Upcoming() {
  
  return (
      <div className='container m-auto'>
        <SectionTitle title='Upcoming Events'/>
        <Swiper
        slidesPerView={4}
        slidesPerGroup={1}
        loop={true}
        loopFillGroupWithBlank={true}
        pagination={{
          clickable: true,
        }}
        breakpoints={{
          320: {
            slidesPerView:1,
          },
          640: {
            slidesPerView:2,
          },
          778: {
            slidesPerView: 3,
          },
          1400: {
            slidesPerView: 4,
          },
        }}
        navigation={true}
        modules={[Pagination, Navigation]}
        className="mySwiper container"
        >
        {upcomingData.map((data,index)=>
        {
        return(
        <SwiperSlide key={index} className="p-3">
        <UpcomingCard  heading={data.heading} description={data.description} dates={data.dates}  imgsrc={data.imgsrc} eventId={data.elink}  />
        </SwiperSlide>
        );
      })}
      </Swiper>
      
    </div>
  )
}

export default Upcoming