import React,{useState} from 'react'
import {Link, useNavigate} from 'react-router-dom'
import axios from 'axios'
import loign from '../../assets/img/login.svg';
import Pulogo from '../../assets/img/pulogo.svg'

// login Page Function
function Login() {
const navigate = useNavigate()
  const [email, setEmail] = useState('')  //email
  const [password, setPassword] = useState('')  //password
  const handleEmail = (e) => {
    setEmail(e.target.value)
  }
  const handlePassword = (e) => {  
    setPassword(e.target.value)
  }       
  // Function to handle the submit event of the form.
  const handleSubmit = (e) => {
    axios.post('https://pueventapi.herokuapp.com/login/user',{email: email, password: password})
    .then(result=>{
      alert('success')      // if the user is successfully logged in, then navigate to the home page.
      localStorage.setItem('token', result.data.token)   // set the token in the local storage
      navigate('/')
    })
    .catch(err=>{
      alert(' service error')  // if the user is not successfully logged in, then show an error message.
       
    })
  }

  return (
    <div className="m-auto justify-center  overflow-hidden ">
     <div className=" flex bg-skin-fill-100 w-full flex-col sm:flex-row p-6 m-auto rounded-3xl shadow-xl  lg:max-w-6xl">  
            <div  className='text-white'>
                <div className="w-full p-6 m-auto rounded-md  lg:max-w-xl">
                <img className=' mb-4' src={Pulogo} alt='logo'>
                </img> 
                <h1 className="text-3xl font-semibold ">
                   Sign in
                </h1>
                <div className="mt-6">
                    <div className="mb-2">
                        <label
                            htmlFor="email"
                            className="block text-sm font-semibold "
                        >
                            Email
                        </label>
                        <input
                            onChange={handleEmail}
                            value={email}
                            type="email"
                            className="block w-full px-4 py-2 mt-2 text-white bg-transparent border border-3 rounded-md focus:border-3 focus:ring-purple-300 focus:outline-none focus:ring focus:ring-opacity-40"
                            placeholder="Email"
                        />
                    </div>
                    <div className="mb-2">
                        <label
                            htmlFor="password"
                            className="block text-sm font-semibold"
                        >
                            Password
                        </label>
                        <input
                            onChange={handlePassword}
                            value={password}
                            type="password"
                            className="block w-full px-4 py-2 mt-2 text-white bg-transparent  border border-3 rounded-md focus:border-3 focus:ring-purple-300 focus:outline-none focus:ring focus:ring-opacity-40"
                            placeholder="Password"
                        />
                    </div>
                    <Link
                        to="/forgetpassword"
                        className="text-xs font-semibold hover:underline"
                    >
                        Forget Password?
                    </Link>
                    <div className="mt-6">
                        <button onClick={handleSubmit} className="w-full px-4 py-2 tracking-wide text-white text-2xl font-bold transition-colors duration-200 transform bg-skin-button-muted rounded-md hover:bg-skin-button-muted/60 focus:outline-none focus:bg-skin-button-muted/60">
                            Login
                        </button>
                    </div>
                </div>
                <p className="mt-8 text-lg font-semibold ">
                    {" "}
                    Don't have an account?{" "}
                    <Link to="/register" className="text-skin-pink hover:underline">
                        Create Account
                    </Link>
                </p>
            </div>
            </div>
            <div className=' self-center'>
                <img src={loign} alt='logo'></img>
            </div>
            
        </div>
    </div>
  )
}

export default Login