import React, { useState } from "react";
import { BrowserRouter as Router,Routes,Route,useLocation, } from "react-router-dom";
import Navbar from "./Components/Navbar";
import Footer from "./Components/Footer";
import Home from "./Pages/Home";
import Events from "./Pages/Events";
import About from "./Pages/About";
import Contactus from "./Pages/Contactus";
import Login from "./Pages/Account/Login";
import Register from "./Pages/Account/Register";
import Error404 from "./Pages/Error/Error404";
import EventsPage from "./Pages/EventsPage";
import ForgetPassword from "./Pages/Account/ForgetPassword";
import Verification from "./Pages/Account/Verification";

//Theme changer buttons  component
const ThemeButtons =({setTheme}
) =>{
  return(
    <div className="absolute flex  right-8 top-30">
      <ThemeCircle setTheme={setTheme} value="" bg="bg-gray-900"/>
      <ThemeCircle setTheme={setTheme} value="light" bg="bg-slate-700"/>
      <ThemeCircle setTheme={setTheme} value="purple" bg="bg-purple-500"/>
    </div>
  )
};

// themecircle component
const ThemeCircle =(props) => {
  return (
        <button className={`h-6 w-6 mr-2 last:mr-0 rounded-full ${props.bg}`}
        onClick= {() => props.setTheme(props.value)}>
      </button>
  )
};
function App() {
  const [theme, setTheme] =useState(""); //theme state
  return ( 
    <div className={theme === "light" ? "theme-light" :theme === "purple" ? "theme-purple" :""}> {/*conditional class for theme colors check   */}
    <div className="bg-skin-fill-50 flex flex-col min-h-screen  font-inter">
      <Router>
         
        {/* <ThemeButtons setTheme={setTheme}/> */}
        <Navbar/>
         <Routes  >  
        <Route path="/" element={<Home />}>  </Route>
        <Route path="/events" element={<Events/>}></Route>
        <Route path="/events/:id" element={<EventsPage/>}></Route>
        <Route path="/about" element={<About/>}></Route>
        <Route path="/contactus" element={<Contactus/>}></Route>
        <Route path="/login" element={<Login/>}></Route>
        <Route path="/register" element={<Register/>}></Route>
        <Route path="*" element={<Error404/>}></Route>
        <Route path="/forgetpassword" element={<ForgetPassword/>}></Route>
        <Route path="/verifyemail" element={<Verification/>}></Route>
        </Routes>

        <Footer/>
      </Router>
        
     </div>
        
  </div>
  );
}

export default App;
