import React from 'react'

function Subscribe() {
  return (
    <div className="container mx-auto  py-8">
      <h5 className=" text-white text-center pt-2 sm:text-sm font-medium ">We are working very hard on the new version of our site.
It will bring a lot of new features. Stay tuned!</h5>
      <div className='mx-auto mt-2 justify-center flex '>
      <input
      type="Your Email"
      className="block pl-2 py-2  text-white bg-transparent border border-3 rounded-md "
      placeholder="Email"
      />
      <button className=" p-2 ml-4 my-auto px-3 tracking-wide text-white text-sm font-bold transition-colors duration-200 transform bg-skin-fill-200 rounded-md hover:bg-skin-button-muted/60 focus:outline-none focus:bg-skin-button-muted/60">
        SIGN UP
      </button>
      </div>
    </div>
  )
}

export default Subscribe